















import { Vue, Component, Prop } from "vue-property-decorator";

@Component({
	components: {}
})
export default class AppTeamsTeamBlock extends Vue {
	@Prop({
		type: String,
		required: true
	}) img!: string;
	@Prop({
		type: String,
		required: true
	}) title!: string;
	@Prop({
		type: String,
		required: true
	}) name!: string;
	@Prop({
		type: String,
		required: true
	}) description!: string;
	@Prop({
		type: Array,
		required: true
	}) materials!: string[];
	@Prop({
		type: Array,
		required: true
	}) classes!: string[];

	get thumbnailUrl() {
		return require(`@/assets/teams/front/${this.img}.png`);
	}
	get backUrl() {
		return require(`@/assets/teams/back/${this.img}.png`);
	}

	get frontStyle() {
		return {
			"background-image": `url(${this.thumbnailUrl})`
		};
	}
	get backStyle() {
		return {
			"background-image": `url(${this.backUrl})`
		};
	}
}
